"use client"

import { useState } from "react"
import { useForm } from "antd/es/form/Form"
import { Button, Form, message } from "antd"
import { useRouter } from "next/navigation"
import { RoutesEnum } from "../../../shared/router/routesEnum"
import { RegisterCredentials } from "../types"
import { registerRequest } from "../api"
import { LeftOutlined } from "@ant-design/icons"
import { UserForm } from "./UserForm"
import { PasswordForm } from "./PasswordForm"
import { DataUserForm } from "./DataForm"

const steps = ["userForm", "dataForm", "passwordForm"] as const
type StepName = (typeof steps)[number]

export const RegisterUserUI = () => {
  const [form] = Form.useForm<RegisterCredentials>()
  const [step, setStep] = useState<StepName>("userForm")
  const [loading, setLoading] = useState(false)
  const router = useRouter()

  const goBack = () => {
    const idx = steps.indexOf(step)
    if (idx > 0) setStep(steps[idx - 1])
  }

  const handleFinish = async (finishedName: string) => {
    const payload = await form.validateFields()
    if (finishedName === "passwordForm") {
      try {
        setLoading(true)
        await registerRequest(payload)
        router.push(RoutesEnum.HOME)
      } catch (e: any) {
        if (e?.errorFields) return // validation errors
        message.error(e?.message || "Ошибка регистрации")
      } finally {
        setLoading(false)
      }
    } else {
      const idx = steps.indexOf(finishedName as StepName)
      setStep(steps[idx + 1])
      form.setFieldsValue({ ...payload })
    }
  }

  const submitCurrent = () => {
    form.submit()
    console.log(form.getFieldsValue())
  }

  return (
    <>
      <Form.Provider
        onFormFinish={name => {
          handleFinish(name as string)
        }}
      >
        {step !== "userForm" && (
          <Button
            onClick={goBack}
            icon={<LeftOutlined />}
          />
        )}

        {step === "userForm" && (
          <UserForm
            form={form}
            name='userForm'
          />
        )}
        {step === "dataForm" && (
          <DataUserForm
            form={form}
            name='dataForm'
          />
        )}
        {step === "passwordForm" && (
          <PasswordForm
            form={form}
            name='passwordForm'
          />
        )}

        <Button
          type='primary'
          block
          loading={loading}
          onClick={submitCurrent}
        >
          {step === "passwordForm" ? "Зарегистрироваться" : "Далее"}
        </Button>
      </Form.Provider>
    </>
  )
}
