"use client"

import FormItem from "antd/es/form/FormItem"

import { Form, FormProps, Input, Select } from "antd"
import { useEffect, useState } from "react"
import { CourseWithoutUsers, GroupWithoutUsers } from "../../types"
import { getCourses, getGroups } from "../../api"

export const DataUserForm: React.FC<FormProps> = ({ ...props }) => {
  return (
    <>
      <Form
        {...props}
        layout='vertical'
      >
        <FormItem
          label={"Курс"}
          name='course'
          required
        >
          <Select
            options={courses.map(course => ({ label: course.identifier, value: course.id }))}
            placeholder='Выберите Курс'
          />
        </FormItem>
        <FormItem
          label={"Группа"}
          name='groups'
          required
          dependencies={["course"]}
        >
          <GroupSelect />
        </FormItem>
      </Form>
    </>
  )
}
