import { AxiosInstance } from "../../../shared/api"
import { TokenResponse } from "../../LoginUser/types"
import { CourseWithoutUsers, GroupWithoutUsers, RegisterCredentials } from "../types"
import { Course } from "../../../shared/entities/Course/course"
import { Group } from "../../../shared/entities/Group/group"

export const registerRequest = async (credentials: RegisterCredentials): Promise<TokenResponse> => {
  return await AxiosInstance.post(`auth/register`, {
    ...credentials,
  })
    .then(response => {
      return response.data
    })
    .catch(error => {
      console.error("Error register user:", error)
      return error
    })
}
export const getGroups = async (): Promise<GroupWithoutUsers[]> => {
  return await AxiosInstance.get(`groups`)
    .then(response => {
      return response.data
    })
    .catch(error => {
      console.error("Error fetching tickets:", error)
      return error
    })
}
export const getCourses = async (): Promise<CourseWithoutUsers[]> => {
  return await AxiosInstance.get(`courses`)
    .then(response => {
      return response.data
    })
    .catch(error => {
      console.error("Error fetching tickets:", error)
      return error
    })
}
